import Vue from 'vue';
import VueRouter from 'vue-router';
import Perceptron from './components/Perceptron.vue';

Vue.use(VueRouter);

export default new VueRouter({
    base: '/',
    mode: 'history',
    routes: [
        { 
            path: '/AI-Tasks', 
            name: "Perceptron",
            component: Perceptron,
        },
        {
            path: '/AI-Tasks/perceptron-training',
            name: "PerceptronTraining",
            component: () => import("./components/PerceptronTraining"),
        },
    ]
});